import axios from 'axios'
import { useEffect, useState } from 'react'
// components
import CardComponent from '../../components/Card'
import GridComponent from '../../components/Grid'
import ListItemComponent from '../../components/ListItem'
import ListItemTextComponent from '../../components/ListItemText'

const Connections = () => {
  const [data, setData] = useState([])

  useEffect(() => {
    axios
      // .get(process.env.REACT_APP_API_URL + '/connections')
      .get('/api/v1/cornerstone/issuer/connections')
      .then(response => {
        // console.log(response.data)
        setData(response.data)
      })
      .catch(error => console.log(error))
  }, [])
  return (
    <GridComponent container justify="center" spacing={2}>
      {data ? (
        data.length === 0 ? (
          <CardComponent
            sx={{
              m: '1rem',
            }}
          >
            No active connections available!
          </CardComponent>
        ) : (
          data.map((connection, index) =>
            connection.state === 'active' ? (
              <GridComponent item xs={12} md={3}>
                <CardComponent
                  key={index}
                  sx={{
                    m: '1rem',
                    overflowWrap: 'break-word',
                  }}
                >
                  <ListItemComponent>
                    <ListItemTextComponent
                      primary={'Invitation date: ' + connection.created_at}
                    />
                  </ListItemComponent>
                  <ListItemComponent>
                    <ListItemTextComponent
                      primary={'Connection ID: ' + connection.connection_id}
                    />
                  </ListItemComponent>
                  <ListItemComponent>
                    <ListItemTextComponent
                      primary={'Connected with: ' + connection.their_label}
                    />
                  </ListItemComponent>
                  <ListItemComponent>
                    <ListItemTextComponent
                      primary={
                        connection.their_label +
                        "'s DID: " +
                        connection.their_did
                      }
                    />
                  </ListItemComponent>
                  <ListItemComponent>
                    <ListItemTextComponent
                      primary={'Connection state: ' + connection.state}
                    />
                  </ListItemComponent>
                  <ListItemComponent sx={{ backgroundColor: '#eee' }}>
                    <ListItemTextComponent
                      primary={'Last updated at: ' + connection.updated_at}
                    />
                  </ListItemComponent>
                </CardComponent>
              </GridComponent>
            ) : (
              ''
            )
          )
        )
      ) : (
        <CardComponent
          sx={{
            m: '1rem',
          }}
        >
          No active connections available!
        </CardComponent>
      )}
    </GridComponent>
  )
}

export default Connections
